adminModule
	.controller('macToolbarController', ['$scope', 'Mac', function($scope, Mac){
		/**
		 *  Object for toolbar view.
		 *
		*/
		$scope.toolbar = {};
		
		/**
		 * Properties of toolbar.
		 *
		*/
		$scope.toolbar.parentState = 'Assets';
		$scope.toolbar.childState = 'Mac';
	}]);